export default function createMessage(type, info) {
  const notifications = {
    "person added": `Added ${info}`,
    "person deleted": `Deleted ${info}`,
    "person updated": `Updated ${info}`,
    "delete error": `${info} has already been deleted from the server`,
    "update error": info,
    "unexpected error": "Something went wrong",
  };

  return notifications[type];
}
